import React from "react";
import { Alert } from "react-bootstrap";
import { useTranslation } from "react-i18next";

export default (args) => {
  /**
   * Default Properties
   */
  let defaults = {};

  /**
   * Properties
   */
  const props = Object.assign(defaults, args.data);

  // console.log("ALERT DATA", props);

  /**
   * Barrels
   */
  const { error, info } = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  const { onAlertClose: onClose } = props.barrelEventHandlers;

  const autoClose = () => {
    window.setTimeout(() => {
      onClose();
    }, 5000);
  };

  if (error !== null) {
    let { t } = useTranslation("Errors");
    return (
      <Alert variant="danger" onClose={() => onClose()} dismissible>
        {t(error)}
      </Alert>
    );
  } else if (info !== null) {
    let { t } = useTranslation("Infos");
    // autoClose();
    return (
      <Alert variant="info" onClose={() => onClose()} dismissible>
        {t(info)}
      </Alert>
    );
  }
  return <></>;
};
