import React from "react";
import { useTranslation } from "react-i18next";
import { Form, InputGroup, Button, Spinner } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import Card from "../card";
import SubmitButton from "../submitButton";

export default ({
  args,
  title,
  navigation,
  navigationdefaultActiveKey,
  onNavigationClick,
}) => {
  /**
   * Default Properties
   */
  let defaults = { request: "transfer U2B", namespace: "Transfer" };

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  // console.log("TRANSFER TO SELF DATA", props);

  /**
   * Barrels
   */
  const {
    request,
    userData,
    accountNumber,
    language,
    accountType,
    currencyAbbr,
  } = props.barrelGetters;
  const { setAccountType } = props.barrelSetters;
  const {
    format,
    parseLocaleNumber,
    sendRequest,
    getReviser,
    getWorkplace,
  } = props.barrelFunctions;
  const { onTransfer } = props.barrelEventHandlers;

  // const userDataParsed = JSON.parse(userData);
  // const userDataParsed = userData;
  const { t } = useTranslation([props.namespace]);

  /** 
   * Destructuring
   ============================================================================ */
  const { currency_abbr } = userData;

  const schema = yup.object({
    fromAccount: yup.string().required(t("From Account is required")),
    // businessTitle: yup
    //   .string()
    //   .required(t("Recipients Business ID is required")),
    purposeID: yup.string().required(t("Purpose ID is required")),
    amount: yup
      .string()
      .required(t("Amount is required"))
      .matches(new RegExp(t("Amount Regex")), t("Wrong amount format")),
  });

  let acc_curr = "";
  let acc_cred = "";
  let acc_save = "";
  if (userData !== null) {
    acc_curr = " (" + format(userData.acc_curr) + ")";
    acc_cred = " (" + format(userData.acc_cred) + ")";
    acc_save = " (" + format(userData.acc_save) + ")";
  }

  /**
   * Transfer to self
   * @param {object} data
   */
  const transferToBusiness = (data) => {
    data.reviser = getReviser();
    data.workplace = getWorkplace();
    data.uid = accountNumber;
    data.amount = parseLocaleNumber(data.amount, language);
    data.amount = new Intl.NumberFormat("en-US", {
      notation: "standard",
      minimumFractionDigits: 2,
    }).format(data.amount);
    // data.amount = parseFloat(data.amount);
    // data.amount = Number(data.amount.replace(/[^0-9.-]+/g, ""));

    sendRequest({ req: props.request, data: data });
    // onTransfer({ req: props.request, data: data });
  };

  const onSubmit = (data) => {
    transferToBusiness(data);
  };

  const TransferToBusinessForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    errors,
  }) => (
    <Card
      title={title}
      navigation={navigation}
      navigationdefaultActiveKey={navigationdefaultActiveKey}
      onNavigationClick={onNavigationClick}
    >
      {/* From account */}
      <Form noValidate onSubmit={handleSubmit}>
        <Form.Group controlId="validationFromAccount">
          <Form.Label>{t("From account")}</Form.Label>
          <Form.Control
            as="select"
            custom
            name="fromAccount"
            onChange={handleChange}
            isInvalid={!!errors.fromAccount && touched.fromAccount}
          >
            <option value="">{t("Choose...")}</option>
            <option
              value="acc_curr"
              hidden={accountType !== null && accountType !== "acc_curr"}
            >
              {t("Current account")}
              {acc_curr}
            </option>
            <option
              value="acc_cred"
              hidden={accountType !== null && accountType !== "acc_cred"}
            >
              {t("Credit account")}
              {acc_cred}
            </option>
            <option
              value="acc_save"
              hidden={accountType !== null && accountType !== "acc_save"}
            >
              {t("Savings account")}
              {acc_save}
            </option>
          </Form.Control>
          <Form.Control.Feedback type="invalid">
            {errors.fromAccount}
          </Form.Control.Feedback>
        </Form.Group>

        {/*
        <Form.Group controlId="validationFormikDestinationAccountID">
          <Form.Label>{t("To Business ID")}</Form.Label>
          <InputGroup>
            <Form.Control
              type="text"
              placeholder=""
              aria-describedby="inputGroupPrepend"
              name="businessTitle"
              value={values.businessTitle}
              onChange={handleChange}
              isInvalid={!!errors.businessTitle && touched.businessTitle}
            />
            <Form.Control.Feedback type="invalid">
              {errors.businessTitle}
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>
        */}

        {/* To purpose */}
        <Form.Group controlId="validationFormikDestinationAccountID">
          <Form.Label>{t("To Purpose ID")}</Form.Label>
          <InputGroup>
            <Form.Control
              type="text"
              placeholder=""
              aria-describedby="inputGroupPrepend"
              name="purposeID"
              value={values.purposeID}
              onChange={handleChange}
              isInvalid={!!errors.purposeID && touched.purposeID}
            />
            <Form.Control.Feedback type="invalid">
              {errors.purposeID}
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>

        {/* Field: Description */}
        <Form.Group
          controlId="description"
          name="description"
          value={values.description}
          onChange={handleChange}
        >
          <Form.Label>{t("Description")}</Form.Label>
          <Form.Control as="textarea" rows="3" />
        </Form.Group>

        {/* Amount */}
        <Form.Group controlId="validationFormikAmount">
          <Form.Label>{t("Amount")}</Form.Label>
          <InputGroup>
            <InputGroup.Prepend>
              <InputGroup.Text id="inputGroupPrepend">
                {currencyAbbr}
              </InputGroup.Text>
            </InputGroup.Prepend>
            <Form.Control
              type="string" // do NOT change to number!!
              placeholder={format(0, "")}
              aria-describedby="inputGroupPrepend"
              name="amount"
              value={values.amount}
              onChange={handleChange}
              isInvalid={!!errors.amount && touched.amount}
            />
            <Form.Control.Feedback type="invalid">
              {errors.amount}
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>

        {/* Submit button */}
        <SubmitButton
          {...args}
          caption={t("Submit")}
          request={props.request}
          namespace={props.namespace}
        />
      </Form>
    </Card>
  );

  return (
    <Formik
      component={TransferToBusinessForm}
      validationSchema={schema}
      onSubmit={onSubmit}
      initialValues={{
        fromAccount: "",
        purposeID: "",
        amount: "",
        description: "",
      }}
    />
  );
};
