/**
 * CSS
 ============================================================================ */
// import "react-bootstrap-table-next/dist/react-bootstrap-table2.min.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { Form, InputGroup, Button, Table, ListGroup } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import { useTranslation } from "react-i18next";
// import BootstrapTable from "react-bootstrap-table-next";
import Skeleton, { SkeletonTheme } from "react-loading-skeleton";

/**
   * User imports
   ============================================================================ */
import Card from "../card";
import SubmitButton from "../submitButton";
import Pagination from "../pagination";
import Shareholder from "../shareholder";

/**
 * Default export
 */
export default (args) => {
  /**
     * Default Properties
     ============================================================================ */
  let defaults = {
    request: "get shares",
    namespace: "Shares",
  };

  /**
     * Properties
     ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
     * Barrels
     ============================================================================ */
  const {
    shares,
    countShares,
    activePageShares,
    sharesPerPage,
    accountNumber,
    userData,
  } = props.barrelGetters;
  const { setActivePageShares } = props.barrelSetters;
  const {
    getReviser,
    getWorkplace,
    sendRequest,
    getShares,
    format,
    formatPerCent,
    formatDate,
  } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
     * Destructured vars
     ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
     * Functions
     ============================================================================ */

  const indication = () => {
    return "THE TABLE IS EMPTY";
  };

  const onPageSelect = (number) => {
    console.log("on page select", number);
    switch (number) {
      case "First":
        number = 1;
        break;
      case "Prev":
        number = activePageShares > 1 ? activePageShares - 1 : 1;
        break;
      case "Next":
        number =
          activePageShares < Math.ceil(countShares / sharesPerPage)
            ? activePageShares + 1
            : Math.ceil(countShares / sharesPerPage);
        break;
      case "Last":
        number = Math.ceil(countShares / sharesPerPage);
        break;
      default:
        // number = number;
        break;
    }
    getShares(number);
  };

  let cnt = 0;

  /**
     * Return
     ============================================================================ */
  return (
    <>
      {shares !== null &&
        shares.map((share, keyShares) => {
          cnt++;

          const {
            business_description,
            business_id,
            business_image,
            business_link,
            business_status,
            business_title,
            owner_firstname,
            owner_lastname,
            owner_status,
            owner_uid,
            purpose_description,
            purpose_id,
            purpose_image,
            purpose_link,
            purpose_status,
            purpose_title,
            shareholder_id,
            shares_created,
            shares_description,
            share_per_cent,
            shares_roles,
            shares_share,
            shares_status,
            shares_title,
          } = share;

          const arrRoles = shares_roles !== null ? shares_roles.split(",") : [];
          const content = `${owner_firstname} ${owner_lastname} / ${formatDate(
            shares_created
          )}`;

          // console.log("keyShares:", keyShares);

          return (
            <Shareholder
              {...args}
              key={keyShares}
              id={keyShares}
              shareholder={shareholder_id}
              purpose={purpose_id}
              firstname={userData.firstname}
              lastname={userData.lastname}
              share={shares_share}
              roles={arrRoles}
              closeButton={false}
              rangeControl={false}
              sharePerCent={share_per_cent}
              businessOwnerFirstname={owner_firstname}
              businessOwnerLastname={owner_lastname}
              businessTitle={business_title}
              purposeTitle={purpose_title}
              sharesCreated={shares_created}
            />
          );
        })}

      {cnt === 0 && <p className="text-center text-muted">{t("No shares")}</p>}

      <Pagination
        {...args}
        pages={Math.ceil(countShares / sharesPerPage)}
        active={activePageShares}
        onClick={onPageSelect}
      />
    </>
  );
};
