/**
 * CSS
 ============================================================================ */
// import "react-bootstrap-table-next/dist/react-bootstrap-table2.min.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { Form, InputGroup, Button, Table } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import { useTranslation } from "react-i18next";
// import BootstrapTable from "react-bootstrap-table-next";

/**
 * User imports
 ============================================================================ */
import Card from "../card";
import SubmitButton from "../submitButton";
import Pagination from "../pagination";

/**
 * Default export
 */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    request: "register currency",
    namespace: "Currencies",
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const {
    currencies,
    countCurrencies,
    activePageCurrencies,
    currenciesPerPage,
    currencyAbbr,
    userData,
  } = props.barrelGetters;
  const { setActivePageCurrencies, setCurrencyAbbr } = props.barrelSetters;
  const {
    getReviser,
    getWorkplace,
    sendRequest,
    getCurrencies,
    formatExchangeRate,
  } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Destructured vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
   * Functions
   ============================================================================ */

  const indication = () => {
    return "THE TABLE IS EMPTY";
  };

  const onCurrencySelected = (e) => {
    setCurrencyAbbr(e.target.id);
    sendRequest({
      req: "register user currency",
      data: { email: userData.email, currency_id: e.target.id },
    });
  };

  const onPageSelect = (number) => {
    console.log("on page select", number);
    switch (number) {
      case "First":
        number = 1;
        break;
      case "Prev":
        number = activePageCurrencies > 1 ? activePageCurrencies - 1 : 1;
        break;
      case "Next":
        number =
          activePageCurrencies < Math.ceil(countCurrencies / currenciesPerPage)
            ? activePageCurrencies + 1
            : Math.ceil(countCurrencies / currenciesPerPage);
        break;
      case "Last":
        number = Math.ceil(countCurrencies / currenciesPerPage);
        break;
      default:
        // number = number;
        break;
    }
    getCurrencies(number);
  };

  /**
   * Form
   ============================================================================ */
  const CurrenciesForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <>
      <Table responsive striped bordered hover size="sm">
        <thead>
          <tr>
            <th style={{ textAlign: "center" }}>{t("Select")}</th>
            <th style={{ textAlign: "center" }}>{t("Abbr")}</th>
            <th style={{ textAlign: "center" }}>{t("Name")}</th>
            <th style={{ textAlign: "center" }}>{t("Region")}</th>
            <th style={{ textAlign: "center" }}>{t("Rate")}</th>
          </tr>
        </thead>
        <tbody>
          {currencies !== null &&
            currencies.map((currency, keyCUrrency) => {
              // console.log("CURRENCY", currency);

              const { name, abbr, rate, region, status, sponsors } = currency;
              return (
                <tr key={keyCUrrency}>
                  <td style={{ textAlign: "center" }}>
                    <Form.Check
                      type="radio"
                      checked={currencyAbbr === abbr}
                      name="selected-currency"
                      id={abbr}
                      aria-label="option 1"
                      onChange={onCurrencySelected}
                    />
                  </td>
                  <td style={{ textAlign: "center" }}>{abbr}</td>
                  <td style={{ textAlign: "left" }}>{name}</td>
                  <td style={{ textAlign: "left" }}>{region}</td>
                  <td style={{ textAlign: "right" }}>
                    {formatExchangeRate(rate)}
                  </td>
                </tr>
              );
            })}
        </tbody>
      </Table>
      <Pagination
        {...args}
        pages={Math.ceil(countCurrencies / currenciesPerPage)}
        active={activePageCurrencies}
        onClick={onPageSelect}
      />
    </>
  );

  /**
   * Formik return
   ============================================================================ */
  // onSubmit={onSubmit}
  return (
    <Formik
      component={CurrenciesForm}
      initialValues={{
        curr_abbr: "",
        curr_title: "",
        curr_region: "",
        curr_rate: 1.0,
      }}
    />
  );
};
