import "bootstrap/dist/css/bootstrap.min.css";
import React from "react";
import { useTranslation } from "react-i18next";
import i18n from "../../translations/i18n";
// import { withNamespaces } from "react-i18next";
import { Form, Badge } from "react-bootstrap";

export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "CurrencyContainer" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { userData } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { sendRequest } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  const { t } = useTranslation("LanguageSelector");
  const changeLanguage = (lng) => {
    i18n.changeLanguage(lng);
  };
  const languages = [
    { id: 1, language: "Latvieu", code: "lv" },
    { id: 2, language: "English", code: "en" },
  ];
  const handleLanguageSelect = (e) => {
    changeLanguage(e.target.value);
  };
  const selectLanguage = (language) => {
    changeLanguage(language);

    if (userData !== null) {
      sendRequest({
        req: "register user language",
        data: { email: userData.email, language },
      });
    }
  };

  if (1) {
    return (
      <>
        <br />
        <Badge
          pill
          variant="light"
          style={{ cursor: "pointer" }}
          onClick={() => {
            selectLanguage("lv");
          }}
        >
          lv
        </Badge>{" "}
        <Badge
          pill
          variant="light"
          style={{ cursor: "pointer" }}
          onClick={() => {
            selectLanguage("en");
          }}
        >
          en
        </Badge>
      </>
    );
  } else {
    return (
      <Form.Control
        as="select"
        id="languageSelector"
        onChange={handleLanguageSelect}
        custom
      >
        <option key="0" value={props.language}>
          {t("Language")}
        </option>
        {languages.map((el) => (
          <option key={el.id} value={el.code}>
            {el.language}
          </option>
        ))}
      </Form.Control>
    );
  }
};

