/**
 * System imports
 ============================================================================ */
import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Form, InputGroup, Button, Spinner } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";

/**
  * User imports
  ============================================================================ */
import SubmitButton from "../submitButton";

/**
  * Default export
  ============================================================================ */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "ResetPassword", request: "reset password" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { language } = props.barrelGetters;
  const { setValidateTokenPasswordReset } = props.barrelSetters;
  const { sendRequest, getUrlParam } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation(props.namespace);

  const token = getUrlParam("token");
  const email = getUrlParam("email");

  /**
    * Effects
    ============================================================================ */
  useEffect(() => {
    if (token === null || email === null) return;

    setValidateTokenPasswordReset(
      JSON.stringify({
        token,
        email,
      })
    );
    // eslint-disable-next-line
  }, []);

  /**
    * Event listeners
    ============================================================================ */

  /**
   * Event listener: On Form Submit
   * @param {object} data Form data
   */
  const onSubmit = (data) => {
    // console.log(data);
    data.language = language;
    sendRequest({
      req: props.request,
      data: data,
    });
  };

  /**
   * Yup schema
   */
  const schema = yup.object({
    email: yup
      .string()
      .email(t("Invalid email address"))
      .required(t("Email is required")),
  });

  const passwordResetForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <Form noValidate onSubmit={handleSubmit}>
      <h3>{t("Reset password")}</h3>

      {/* Email */}
      <Form.Group controlId="validationFormikEmail">
        <Form.Label>{t("Email address")}</Form.Label>
        <InputGroup>
          <Form.Control
            placeholder={t("Enter email")}
            name="email"
            value={values.email}
            onChange={handleChange}
            // onBlur={handleChange}
            isInvalid={!!errors.email && touched.email}
          />
          <Form.Control.Feedback type="invalid">
            {errors.email}
          </Form.Control.Feedback>
        </InputGroup>
      </Form.Group>

      <Form.Group controlId="validationFormikPassword">
        <SubmitButton
          {...args}
          caption={t("Submit")}
          request={props.request}
          namespace={props.namespace}
        />
      </Form.Group>
    </Form>
  );

  // return (
  //   <Formik
  //     component={passwordResetForm}
  //     validationSchema={schema}
  //     onSubmit={onSubmit}
  //     initialValues={{
  //       email: "",
  //     }}
  //   />
  // );

  return token === null ? (
    <Formik
      component={passwordResetForm}
      validationSchema={schema}
      onSubmit={onSubmit}
      initialValues={{
        email: "",
      }}
    />
  ) : (
    <div className="text-center">
      <Spinner
        as="span"
        animation="border"
        size="sm"
        role="status"
        aria-hidden="true"
        hidden={false}
      />
    </div>
  );
};
