import React from "react";
import { useTranslation, Trans } from "react-i18next";
import { Form, InputGroup, Button, Alert } from "react-bootstrap";
// import { Redirect } from "react-router-dom";
import Card from "../card";
import { Formik } from "formik";
import * as yup from "yup";
// import { useStickyState } from "../../utils/state";
import Dropzone from "../dropzone";
import SubmitButton from "../submitButton";

export default (args) => {
  // console.log("ACCOUNT REGISTRATION ARGS:", args);
  /**
   * Default Properties
   */
  let defaults = {
    request: "register account",
    namespace: "AccountRegistration",
  };

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   */
  const {
    signedIn,
    accountNumber,
    accountPhotoPath,
    userData,
  } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { getReviser, getWorkplace, sendRequest } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Hooks
   */
  // const [redirect, setRedirect] = useState(false);

  /**
   * Custom hooks
   */
  const { t } = useTranslation(props.namespace);

  /**
   * Yup schema
   */
  const schema = yup.object({
    dropzoneHook: yup.string().required(t("Photo ID is required")),
    personalID: yup
      .string()
      .required(t("Personal ID is required"))
      .matches(
        new RegExp(t("Personal ID Regex")),
        t("Wrong Personal ID format")
      )
      .test({
        name: "integrity",
        exclusive: true,
        params: {},
        message: t("PersonalID must be valid"),
        test: (value) => {
          if (typeof value === "undefined") return false;
          let pid = value
            .replace("-", "")
            .split("")
            .map(function (n) {
              return parseInt(n, 10);
            });
          let calc =
            1 * pid[0] +
            6 * pid[1] +
            3 * pid[2] +
            7 * pid[3] +
            9 * pid[4] +
            10 * pid[5] +
            5 * pid[6] +
            8 * pid[7] +
            4 * pid[8] +
            2 * pid[9];
          let checksum = (1101 - calc) % 11;

          return checksum == pid[10];
        },
      }),
  });

  /**
   * Functions
   */

  /**
   * Send account registration request
   * @param {object} data
   */
  const sendAccountRegistrationRequest = (data) => {
    if (data === null) return false;

    data.reviser = getReviser();
    data.workplace = getWorkplace();
    // data.data = userData;
    // data.email = JSON.parse(userData).email;
    data.email = userData.email;

    return sendRequest({ req: props.request, data });
    // return onAccountRegistration(data);
  };

  /**
   * Event listener: On Form Submit
   * @param {object} data Form data
   */
  const onSubmit = (data) => {
    if (sendAccountRegistrationRequest(data)) {
      // Account registration request was submitted
    }
  };

  // let dropzoneUntouched = true;

  const AccountRegistrationForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    setFieldValue,
    errors,
  }) => (
    <Form noValidate onSubmit={handleSubmit}>
      <Form.Group controlId="validationFormikPhotoId">
        <Form.Label>{t("Photo ID")}</Form.Label>

        <Form.Text>
          <Trans i18nKey="AccountRegistration:Photo ID requirements">
            Photo of me holding my passport. The personal identification number
            must be clearly legible.
          </Trans>
        </Form.Text>

        <br />
        <Dropzone data={props} />
      </Form.Group>

      <Form.Group controlId="validationFormikDropzoneHook">
        <Form.Control
          type="text"
          name="dropzoneHook"
          value={accountPhotoPath}
          onChange={handleChange}
          isInvalid={!!errors.dropzoneHook}
          style={{ display: "none" }}
        />

        <Form.Control.Feedback type="invalid">
          {errors.dropzoneHook}
        </Form.Control.Feedback>
      </Form.Group>

      {false && (
        <Form.Group controlId="validationFormikCountry">
          <Form.Label>{t("Country")}</Form.Label>
          <Form.Control as="select" custom>
            <option>Latvija</option>
            <option>Deutschland</option>
          </Form.Control>
        </Form.Group>
      )}

      <Form.Group controlId="validationFormikPersonalId">
        <Form.Label>{t("Personal ID")}</Form.Label>
        <InputGroup>
          <InputGroup.Prepend>
            <InputGroup.Text id="inputGroupPrepend">ID</InputGroup.Text>
          </InputGroup.Prepend>
          <Form.Control
            type="text"
            placeholder={t("XXXXXX-XXXXX")}
            aria-describedby="inputGroupPrepend"
            name="personalID"
            value={values.personalID}
            onChange={handleChange}
            isInvalid={!!errors.personalID && touched.personalID}
          />
          <Form.Control.Feedback type="invalid">
            {errors.personalID}
          </Form.Control.Feedback>
        </InputGroup>
      </Form.Group>
      <Form.Group>
        <Form.Check
          required
          name="terms"
          label={t("Agree to terms and conditions")}
          onChange={handleChange}
          isInvalid={!!errors.terms && touched.terms}
          feedback={errors.terms}
          id="validationFormik0"
        />
      </Form.Group>

      <SubmitButton
        {...args}
        caption={t("Submit")}
        request={props.request}
        namespace={props.namespace}
      />
    </Form>
  );

  if (signedIn === false)
    return (
      <>
        <Alert variant="info">
          <Alert.Heading>{t("User not logged in")}</Alert.Heading>
          <p>
            <Trans i18nKey="AccountRegistration:Please log in first">
              Please log in first.
            </Trans>
          </p>
        </Alert>
      </>
    );

  if (accountNumber !== null)
    return (
      <>
        <Alert variant="info">
          <Alert.Heading>{t("Account successfully registered")}</Alert.Heading>
          <p>
            <Trans
              i18nKey="AccountRegistration:Account with your personal ID successfully registered"
              accountNumber={accountNumber}
            >
              Account with the personal ID
              <strong>{{ accountNumber }}</strong>
              successfully registered.
            </Trans>
          </p>
        </Alert>
      </>
    );

  return (
    <Card title={t("Open account")}>
      <Formik
        component={AccountRegistrationForm}
        validationSchema={schema}
        onSubmit={onSubmit}
        initialValues={{
          personalID: "",
          terms: "",
          dropzoneHook: accountPhotoPath,
        }}
      />
    </Card>
  );
};
