import React from "react";
import { useTranslation } from "react-i18next";
import { Form, InputGroup, Button, Spinner } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import Card from "../card";
import SubmitButton from "../submitButton";

export default ({
  args,
  title,
  navigation,
  navigationdefaultActiveKey,
  onNavigationClick,
}) => {
  /**
   * Default Properties
   */
  let defaults = {
    request: "transfer U2S",
    namespace: "Transfer",
  };

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  // console.log("TRANSFER TO SELF DATA", props);

  /**
   * Barrels
   */
  const {
    request,
    userData,
    accountNumber,
    accountType,
    language,
    currencyAbbr,
  } = props.barrelGetters;
  const { setAccountType } = props.barrelSetters;
  const {
    format,
    parseLocaleNumber,
    sendRequest,
    getReviser,
    getWorkplace,
  } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  // const userDataParsed = JSON.parse(userData);
  // const userData = userData;
  const { t } = useTranslation([props.namespace]);

  const { currency_abbr } = userData;

  /** 
   * Yup schema
   ============================================================================ */
  const schema = yup.object({
    fromAccount: yup.string().required(t("From Account is required")),
    toAccount: yup.string().required(t("To Account is required")),
    amount: yup
      .string()
      .required(t("Amount is required"))
      .matches(new RegExp(t("Amount Regex")), t("Wrong amount format")),
  });

  let acc_curr = "";
  let acc_cred = "";
  let acc_save = "";
  if (userData !== null) {
    acc_curr = " (" + format(userData.acc_curr) + ")";
    acc_cred = " (" + format(userData.acc_cred) + ")";
    acc_save = " (" + format(userData.acc_save) + ")";
  }

  /**
   * Transfer to self
   * @param {object} Form data
   */
  const transferToSelf = (data) => {
    // console.log("TRANSFER U2S DATA:", data);
    data.reviser = getReviser();
    data.workplace = getWorkplace();
    data.uid = accountNumber;
    data.amount = parseLocaleNumber(data.amount, language);
    data.amount = new Intl.NumberFormat("en-US", {
      notation: "standard",
      minimumFractionDigits: 2,
    }).format(data.amount);

    // onTransfer({ req: props.request, data: data });
    sendRequest({ req: props.request, data: data });
  };

  const onSubmit = (data) => {
    transferToSelf(data);
  };

  const TransferToSelfForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <Card
      title={title}
      navigation={navigation}
      navigationdefaultActiveKey={navigationdefaultActiveKey}
      onNavigationClick={onNavigationClick}
    >
      <Form noValidate onSubmit={handleSubmit}>
        <Form.Group controlId="validationFromAccount">
          <Form.Label>{t("From account")}</Form.Label>
          <Form.Control
            as="select"
            custom
            name="fromAccount"
            onChange={handleChange}
            isInvalid={!!errors.fromAccount && touched.fromAccount}
          >
            <option value="">{t("Choose...")}</option>
            <option
              value="acc_curr"
              hidden={accountType !== null && accountType !== "acc_curr"}
            >
              {t("Current account")}
              {acc_curr}
            </option>
            <option
              value="acc_cred"
              hidden={accountType !== null && accountType !== "acc_cred"}
            >
              {t("Credit account")}
              {acc_cred}
            </option>
            <option
              value="acc_save"
              hidden={accountType !== null && accountType !== "acc_save"}
            >
              {t("Savings account")}
              {acc_save}
            </option>
          </Form.Control>
          <Form.Control.Feedback type="invalid">
            {errors.fromAccount}
          </Form.Control.Feedback>
        </Form.Group>
        <Form.Group controlId="validationToAccount">
          <Form.Label>{t("To account")}</Form.Label>
          <Form.Control
            as="select"
            custom
            name="toAccount"
            onChange={handleChange}
            isInvalid={!!errors.toAccount && touched.toAccount}
          >
            <option value="">{t("Choose...")}</option>
            <option value="acc_curr" hidden={accountType === "acc_curr"}>
              {t("Current account")}
              {acc_curr}
            </option>
            <option value="acc_cred" hidden={accountType === "acc_cred"}>
              {t("Credit account")}
              {acc_cred}
            </option>
            <option value="acc_save" hidden={accountType === "acc_save"}>
              {t("Savings account")}
              {acc_save}
            </option>
          </Form.Control>
          <Form.Control.Feedback type="invalid">
            {errors.toAccount}
          </Form.Control.Feedback>
        </Form.Group>
        <Form.Group controlId="validationFormikAmount">
          <Form.Label>{t("Amount")}</Form.Label>
          <InputGroup>
            <InputGroup.Prepend>
              <InputGroup.Text id="inputGroupPrepend">
                {currencyAbbr}
              </InputGroup.Text>
            </InputGroup.Prepend>
            <Form.Control
              // type="number"
              placeholder={format(0, "")}
              aria-describedby="inputGroupPrepend"
              name="amount"
              value={values.amount}
              onChange={handleChange}
              // onBlur={handleChange}
              isInvalid={!!errors.amount && touched.amount}
            />
            <Form.Control.Feedback type="invalid">
              {errors.amount}
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>

        <SubmitButton
          {...args}
          caption={t("Submit")}
          request="transfer U2S"
          namespace={props.namespace}
        />
      </Form>
    </Card>
  );

  return (
    <Formik
      component={TransferToSelfForm}
      validationSchema={schema}
      onSubmit={onSubmit}
      initialValues={{
        fromAccount: "",
        toAccount: "",
        amount: "",
      }}
    />
  );
};
