/**
 * System imports
 ============================================================================ */
import React, { useState, useRef } from "react";
import { useTranslation } from "react-i18next";
import { Form, Toast, Badge } from "react-bootstrap";
import { Settings } from "@material-ui/icons";
import _ from "lodash";

/**
  * User imports
  ============================================================================ */

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = {
    namespace: "Shareholder",
    request: "change share",
    purpose: null,
    shareholder: null,
    closeButton: true,
    rangeControl: true,
    sharePerCent: null,
    businessTitle: null,
    purposeTitle: null,
    businessOwnerFirstname: null,
    businessOwnerLastname: null,
    sharesCreated: null,
  };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { purposeShareholders } = props.barrelGetters;
  const { setPurposeShareholders } = props.barrelSetters;
  const { sendRequest, formatPerCent, formatDate } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Barrels
    ============================================================================ */
  const [userQuery, setUserQuery] = useState(props.share);
  const delayedQuery = useRef(_.debounce((q) => sendQuery(q), 2000)).current;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  const onChange = (e) => {
    // console.log(props.shareholder, e.target.value);
    setUserQuery(e.target.value);
    delayedQuery(e.target.value);
  };

  const onClose = () => {
    // console.log("delete", props.purpose, props.shareholder);
    let data = {};
    data.purpose_id = props.purpose;
    data.shareholder_id = props.shareholder;
    return sendRequest({ req: "remove shareholder", data });
  };

  const sendQuery = (delayedQuery) => {
    // console.log(props.purpose, props.shareholder, delayedQuery);
    const ps = purposeShareholders;
    ps[props.id].share = delayedQuery;
    setPurposeShareholders(JSON.stringify(ps));
    let data = {};
    data.purpose_id = props.purpose;
    data.shareholder_id = props.shareholder;
    data.share = delayedQuery;
    // console.log(data);
    return sendRequest({ req: props.request, data });
  };

  /**
    * Functions
    ============================================================================ */
  const getJSX = () => {
    const sharesLabelId = `sharesLabel${props.key}`;
    return (
      <Toast key={props.key} onClose={onClose} style={{ minWidth: "100%" }}>
        <Toast.Header closeButton={props.closeButton}>
          <strong className="mr-auto">
            {props.firstname} {props.lastname}{" "}
            <small>
              <Settings
                hidden={true}
                fontSize="small"
                style={{ color: "var(--dark)" }}
              />
            </small>
          </strong>
          <small>
            <span id={sharesLabelId}>{userQuery}</span> {t("shares")}
          </small>
        </Toast.Header>
        <Toast.Body>
          <Form>
            <Form.Group controlId="formRoles">
              <div>
                {props.roles.map((role, key) => {
                  return (
                    <span key={key}>
                      <Badge pill variant="secondary">
                        {role}
                      </Badge>{" "}
                    </span>
                  );
                })}
              </div>
            </Form.Group>
            {props.rangeControl && (
              <Form.Group controlId="formBasicRange">
                <Form.Control
                  type="range"
                  variant="light"
                  // value={share}
                  defaultValue={userQuery}
                  // value={userQuery}
                  min={0}
                  max={1000}
                  step={1}
                  // ref={this.input}
                  onChange={onChange}
                />
              </Form.Group>
            )}
          </Form>
          <span>
            {props.sharePerCent !== null && formatPerCent(props.sharePerCent)}{" "}
            {props.businessTitle !== null && props.businessTitle}{" "}
            {props.businessTitle !== null && props.purposeTitle !== null && (
              <> &middot; </>
            )}
            {props.purposeTitle !== null && props.purposeTitle}{" "}
          </span>
          {props.sharesCreated !== null && (
            <>
              <br />
              {formatDate(props.sharesCreated, {
                month: "long",
                day: "numeric",
                year: "numeric",
              })}
            </>
          )}{" "}
          {(props.businessOwnerFirstname !== null ||
            props.businessOwnerLastname !== null) && <br />}
          {props.businessOwnerFirstname !== null &&
            props.businessOwnerFirstname}{" "}
          {props.businessOwnerLastname !== null && props.businessOwnerLastname}{" "}
        </Toast.Body>
      </Toast>
    );
  };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
};
