/**
 * System imports
 */
import React from "react";
import { useTranslation } from "react-i18next";
import { Form, InputGroup, Button, Spinner } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import SubmitButton from "../submitButton";

/**
 * User imports
 */
import Card from "../card";

/**
 * Component: TransferToPerson
 */
export default ({
  args,
  title,
  navigation,
  navigationdefaultActiveKey,
  onNavigationClick,
}) => {
  /**
   * Default Properties
   */
  let defaults = { request: "transfer U2U", namespace: "Transfer" };

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   */
  const {
    request,
    userData,
    accountNumber,
    language,
    accountType,
    currencyAbbr,
  } = props.barrelGetters;
  const { setAccountType } = props.barrelSetters;
  const {
    format,
    parseLocaleNumber,
    sendRequest,
    getReviser,
    getWorkplace,
  } = props.barrelFunctions;
  const { onTransfer } = props.barrelEventHandlers;

  /** 
   * Custom hooks
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /** 
   * Destructuring
   ============================================================================ */
  const { currency_abbr } = userData;

  /** 
   * Yup schema
   ============================================================================ */
  const schema = yup.object({
    fromAccount: yup.string().required(t("From Account is required")),
    toPersonalID: yup
      .string()
      .required(t("Recipients Personal ID is required"))
      .matches(
        new RegExp(t("Personal ID Regex")),
        t("Wrong Personal ID format")
      ),
    amount: yup
      .string()
      .required(t("Amount is required"))
      .matches(new RegExp(t("Amount Regex")), t("Wrong amount format")),
  });

  let acc_curr = "";
  let acc_cred = "";
  let acc_save = "";
  if (userData !== null) {
    acc_curr = " (" + format(userData.acc_curr) + ")";
    acc_cred = " (" + format(userData.acc_cred) + ")";
    acc_save = " (" + format(userData.acc_save) + ")";
  }

  /**
   * Transfer to self
   * @param {object} data
   */
  const transferToPerson = (data) => {
    // console.log("TRANSFER U2U DATA:", data);
    data.reviser = getReviser();
    data.workplace = getWorkplace();
    data.uid = accountNumber;
    data.amount = parseLocaleNumber(data.amount, language);
    data.amount = new Intl.NumberFormat("en-US", {
      notation: "standard",
      minimumFractionDigits: 2,
    }).format(data.amount);

    sendRequest({ req: props.request, data: data });
    // onTransfer({ req: props.request, data: data });
  };

  // return <>test</>;

  const onSubmit = (data) => {
    transferToPerson(data);
  };

  const TransferToPersonForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    errors,
  }) => (
    <Card
      title={title}
      navigation={navigation}
      navigationdefaultActiveKey={navigationdefaultActiveKey}
      onNavigationClick={onNavigationClick}
    >
      <Form noValidate onSubmit={handleSubmit}>
        {/* Field: From account */}
        <Form.Group controlId="validationFromAccount">
          <Form.Label>{t("From account")}</Form.Label>
          <Form.Control
            as="select"
            custom
            name="fromAccount"
            onChange={handleChange}
            isInvalid={!!errors.fromAccount && touched.fromAccount}
          >
            <option value="">{t("Choose...")}</option>
            <option
              value="acc_curr"
              hidden={accountType !== null && accountType !== "acc_curr"}
            >
              {t("Current account")}
              {acc_curr}
            </option>
            <option
              value="acc_cred"
              hidden={accountType !== null && accountType !== "acc_cred"}
            >
              {t("Credit account")}
              {acc_cred}
            </option>
            <option
              value="acc_save"
              hidden={accountType !== null && accountType !== "acc_save"}
            >
              {t("Savings account")}
              {acc_save}
            </option>
          </Form.Control>
          <Form.Control.Feedback type="invalid">
            {errors.fromAccount}
          </Form.Control.Feedback>
        </Form.Group>

        {/* Field: Recipients Personal ID */}
        <Form.Group controlId="validationFormikToPersonalID">
          <Form.Label>{t("To Personal ID")}</Form.Label>
          <InputGroup>
            <InputGroup.Prepend>
              <InputGroup.Text id="inputGroupPrepend">ID</InputGroup.Text>
            </InputGroup.Prepend>
            <Form.Control
              type="text"
              placeholder="XXXXXX-XXXXX"
              aria-describedby="inputGroupPrepend"
              name="toPersonalID"
              value={values.toPersonalID}
              onChange={handleChange}
              isInvalid={!!errors.toPersonalID && touched.toPersonalID}
            />
            <Form.Control.Feedback type="invalid">
              {errors.toPersonalID}
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>

        {/* Field: Description */}
        <Form.Group
          controlId="description"
          name="description"
          value={values.description}
          onChange={handleChange}
        >
          <Form.Label>{t("Description")}</Form.Label>
          <Form.Control as="textarea" rows="3" />
        </Form.Group>

        {/* Field: Amount */}
        <Form.Group controlId="validationFormikAmount">
          <Form.Label>{t("Amount")}</Form.Label>
          <InputGroup>
            <InputGroup.Prepend>
              <InputGroup.Text id="inputGroupPrepend">
                {currencyAbbr}
              </InputGroup.Text>
            </InputGroup.Prepend>
            <Form.Control
              // type="number"
              placeholder={format(0, "")}
              aria-describedby="inputGroupPrepend"
              name="amount"
              value={values.amount}
              onChange={handleChange}
              // onBlur={handleChange}
              isInvalid={!!errors.amount && touched.amount}
            />
            <Form.Control.Feedback type="invalid">
              {errors.amount}
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>

        {/* Button: Next */}

        <SubmitButton
          {...args}
          caption={t("Submit")}
          request={props.request}
          namespace={props.namespace}
        />
      </Form>
    </Card>
  );

  return (
    <Formik
      component={TransferToPersonForm}
      validationSchema={schema}
      onSubmit={onSubmit}
      initialValues={{
        fromAccount: "",
        toPersonalID: "",
        amount: "",
        description: "",
      }}
    />
  );
};
