import React, { useState, useEffect } from "react";
import { useTranslation, Trans } from "react-i18next";
import { TransferToSelf, TransferToPerson, TransferToBusiness } from "./index";
import { Alert } from "react-bootstrap";

// const navigation = [
//   {
//     name: "U2S",
//     link: "#u2s",
//   },
//   {
//     name: "U2U",
//     link: "#u2u",
//   },
//   {
//     name: "U2B",
//     link: "#u2b",
//   },
// ];

/**
 * Component: Transfer
 */
export default (args) => {
  /**
   * Default Properties
   */
  let defaults = {};

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  // console.log("TRANSFER DATA", props);

  /**
   * Barrels
   */
  const { accountNumber, transferType } = props.barrelGetters;
  const { setRedirectTo } = props.barrelSetters;
  const { format } = props.barrelFunctions;
  const { onTransferTypeChange } = props.barrelEventHandlers;

  const { t } = useTranslation("Transfer");

  const data = props.data;
  const type = transferType;
  // const onTypeChange = args.onTypeChange;

  // console.log("TRANSFER DATA TYPE:", type);

  const [transferCard, setTransferCard] = useState(null);

  const onNavigationClick = (id) => {
    onTransferTypeChange(id);
  };

  const navigation = [
    {
      name: t("U2S"),
      link: "#u2s",
    },
    {
      name: t("U2U"),
      link: "#u2u",
    },
    {
      name: t("U2B"),
      link: "#u2b",
    },
  ];

  // console.log("TRANSFER DATA", data);
  // const title = t("Transfer to account");

  useEffect(() => {
    if (type === null) return;

    // setTransferCard()
    switch (type) {
      case "#u2s": // User to self
        setTransferCard(
          <TransferToSelf
            title={t("Transfer to account")}
            args={props}
            navigation={navigation}
            navigationdefaultActiveKey={type}
            onNavigationClick={onNavigationClick}
          />
        );
        break;
      case "#u2u": // User to user
        setTransferCard(
          <TransferToPerson
            title={t("Transfer to account")}
            args={props}
            navigation={navigation}
            navigationdefaultActiveKey={type}
            onNavigationClick={onNavigationClick}
          />
        );
        break;
      case "#u2b": // User to business
        setTransferCard(
          <TransferToBusiness
            title={t("Transfer to account")}
            args={props}
            navigation={navigation}
            navigationdefaultActiveKey={type}
            onNavigationClick={onNavigationClick}
          />
        );
        break;
      default:
        break;
    }

    // eslint-disable-next-line
  }, [data, format, type]);

  if (accountNumber === null) setRedirectTo("/sign-out");

  // return (
  //   <>
  //     <Alert variant="warning">
  //       <Alert.Heading>{t("Account not yet registered")}</Alert.Heading>
  //       <p>
  //         <Trans i18nKey="Balance:Please set up your account first.">
  //           Please set up your account first. Use this{" "}
  //           <a href="/account-registration">link</a>.
  //         </Trans>
  //       </p>
  //     </Alert>
  //   </>
  // );

  return <>{transferCard}</>;
};
