/**
 * System imports
 ============================================================================ */
import React, { useState } from "react";
import { Form, InputGroup, Button } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import { useTranslation } from "react-i18next";
import { Visibility, VisibilityOff } from "@material-ui/icons";

/**
   * User imports
   ============================================================================ */
import SubmitButton from "../submitButton";
import Card from "../card";

/**
 * Default export
 */
export default (args) => {
  /**
     * Default Properties
     ============================================================================ */
  let defaults = {
    namespace: "ChangePassword",
    request: "change password",
    email: null,
  };

  /**
     * Properties
     ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
     * Barrels
     ============================================================================ */
  const {
    passwordResetTokenValidated,
    passwordResetTokenEmail,
  } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { sendRequest } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
     * Destructured vars
     ============================================================================ */
  const { t } = useTranslation([
    passwordResetTokenValidated === true ? "SignUp" : "ChangePassword",
  ]); // adjust namespace

  /**
   * Hooks
   */
  const [passwordVisible, setPasswordVisible] = useState(false);

  /**
     * Functions
     ============================================================================ */
  const onSubmit = (data) => {
    data.email = passwordResetTokenEmail;
    // console.log(data);
    return sendRequest({ req: props.request, data });
  };

  const togglePassword = () => {
    setPasswordVisible(!passwordVisible);
  };

  /** 
     * Yup schema
     ============================================================================ */
  const schema = yup.object({
    password: yup
      .string()
      .required(t("No password provided"))
      .min(8, t("Password is too short"))
      // .matches(/[a-zA-Z]/, "Password can only contain Latin letters.")
      .matches(
        /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#_?&])[A-Za-z\d@$!%*#_?&]{8,}$/,
        t("Password is too weak")
      ),
  });

  /**
     * Form
     ============================================================================ */
  const ChangePasswordForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <Form noValidate onSubmit={handleSubmit}>
      {/* Password */}
      <Form.Group controlId="validationFormikPassword">
        <Form.Label>{t("New password")}</Form.Label>
        <InputGroup>
          <Form.Control
            type={passwordVisible ? "text" : "password"}
            placeholder={t("Enter password")}
            name="password"
            value={values.password}
            onChange={handleChange}
            // onBlur={handleChange}
            isInvalid={!!errors.password && touched.password}
          />
          <InputGroup.Append>
            <InputGroup.Text
              id="inputGroupAppend"
              onClick={togglePassword}
              style={{
                cursor: "pointer",
              }}
            >
              {passwordVisible ? (
                <VisibilityOff
                  fontSize="small"
                  style={{ color: "var(--dark)" }}
                />
              ) : (
                <Visibility fontSize="small" style={{ color: "var(--dark)" }} />
              )}
            </InputGroup.Text>
          </InputGroup.Append>

          <Form.Control.Feedback type="invalid">
            {errors.password}
          </Form.Control.Feedback>
        </InputGroup>
      </Form.Group>

      <SubmitButton
        {...args}
        caption={t("Submit")}
        request={props.request}
        namespace={props.namespace}
      />
    </Form>
  );

  /**
     * Return
     ============================================================================ */
  if (passwordResetTokenValidated === true) {
    // if (true) {
    return (
      <Formik
        component={ChangePasswordForm}
        validationSchema={schema}
        onSubmit={onSubmit}
        initialValues={{
          password: "",
        }}
      />
    );
  } else {
    // if (passwordResetTokenValidated === false)
    return <>{t("Awaiting validation")}</>;
    // } else {
    //   // null
    //   return <>How did you land here???</>;
  }
};
