/**
 * CSS
 ============================================================================ */
// import "react-bootstrap-table-next/dist/react-bootstrap-table2.min.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import {
  Form,
  InputGroup,
  Button,
  Table,
  ListGroup,
  Badge,
} from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import { useTranslation } from "react-i18next";
// import BootstrapTable from "react-bootstrap-table-next";

/**
  * User imports
  ============================================================================ */
import Card from "../card";
import SubmitButton from "../submitButton";
import Pagination from "../pagination";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = {
    request: "get transactions",
    namespace: "Transactions",
  };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const {
    transactions,
    countTransactions,
    activePageTransactions,
    transactionsPerPage,
    accountNumber,
  } = props.barrelGetters;
  const { setActivePageTransactions } = props.barrelSetters;
  const {
    getReviser,
    getWorkplace,
    sendRequest,
    getTransactions,
    format,
    formatDate,
    formatPerCent,
  } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
    * Functions
    ============================================================================ */

  const indication = () => {
    return "THE TABLE IS EMPTY";
  };

  const onPageSelect = (number) => {
    console.log("on page select", number);
    switch (number) {
      case "First":
        number = 1;
        break;
      case "Prev":
        number = activePageTransactions > 1 ? activePageTransactions - 1 : 1;
        break;
      case "Next":
        number =
          activePageTransactions <
          Math.ceil(countTransactions / transactionsPerPage)
            ? activePageTransactions + 1
            : Math.ceil(countTransactions / transactionsPerPage);
        break;
      case "Last":
        number = Math.ceil(countTransactions / transactionsPerPage);
        break;
      default:
        // number = number;
        break;
    }
    getTransactions(number);
  };

  let cnt = 0;

  /**
    * Return
    ============================================================================ */
  return (
    <>
      <ListGroup variant="flush">
        {transactions !== null &&
          transactions.map((transaction, keyTransactions) => {
            cnt++;

            const {
              business_description,
              business_id,
              business_image,
              business_link,
              business_status,
              business_title,
              comment,
              conv_amount,
              created,
              currency,
              exchange_rate,
              sender_firstname,
              sender_lastname,
              recipient_firstname,
              recipient_lastname,
              purpose_description,
              purpose_id,
              purpose_image,
              purpose_link,
              purpose_status,
              purpose_title,
              recipient_id,
              roles,
              share,
              share_per_cent,
              sender_id,
              sender_status,
              recipient_status,
              to_account,
              type,
            } = transaction;

            const i_am_sender = sender_id === accountNumber;
            const i_am_recipient = recipient_id === accountNumber;

            const roles_available =
              roles !== null && roles !== "-" && typeof roles !== "undefined";
            const arrRoles = roles_available ? roles.split(",") : [];

            const shares_available =
              typeof share !== "undefined" && share !== null && share !== "-";

            if (type === "transfer U2S") {
              return (
                <ListGroup.Item key={keyTransactions} action>
                  {sender_firstname} {sender_lastname}
                  <span style={{ float: "right" }}>
                    {i_am_recipient ? "" : "-"}
                    {format(conv_amount)}
                  </span>
                  <br />
                  <small>{formatDate(created)}</small>
                </ListGroup.Item>
              );
            }

            if (type === "transfer U2U") {
              return (
                <ListGroup.Item key={keyTransactions} action>
                  {sender_firstname} {sender_lastname}
                  <span style={{ float: "right" }}>
                    {i_am_recipient ? "" : "-"}
                    {format(conv_amount)}
                  </span>
                  <br />
                  <small>{formatDate(created)}</small>
                </ListGroup.Item>
              );
            }

            if (type === "transfer U2B") {
              return (
                <ListGroup.Item key={keyTransactions} action>
                  {purpose_id}
                  <span style={{ float: "right" }}>
                    {i_am_recipient ? "" : "-"}
                    {format(conv_amount)}
                  </span>
                  <br />
                  {business_title} &middot; {purpose_title}
                  <br />
                  {sender_firstname} {sender_lastname}
                  {roles_available && (
                    <>
                      <br />
                      {arrRoles.map((role, key) => {
                        return (
                          <span key={key}>
                            {" "}
                            <Badge pill variant="secondary">
                              {role}
                            </Badge>
                          </span>
                        );
                      })}
                    </>
                  )}{" "}
                  {shares_available && (
                    <>
                      <br />
                      {share} {t("shares")} ({formatPerCent(share_per_cent)})
                    </>
                  )}
                  <br />
                  <small>{formatDate(created)}</small>
                </ListGroup.Item>
              );
            }
          })}
      </ListGroup>

      {cnt === 0 && (
        <p className="text-center text-muted">{t("No transactions")}</p>
      )}

      <br />
      <Pagination
        {...args}
        pages={Math.ceil(countTransactions / transactionsPerPage)}
        active={activePageTransactions}
        onClick={onPageSelect}
      />
    </>
  );
};
