/**
 * CSS
 ============================================================================ */
// import "react-bootstrap-table-next/dist/react-bootstrap-table2.min.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { Form, InputGroup, Button, Table } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import { useTranslation } from "react-i18next";
// import BootstrapTable from "react-bootstrap-table-next";

/**
 * User imports
 ============================================================================ */
import Card from "../card";
import SubmitButton from "../submitButton";
import Pagination from "../pagination";

/**
 * Default export
 */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    namespace: "CurrencyRegistration",
    request: "register currency",
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const {
    currencies,
    countCurrencies,
    activePageCurrencies,
    currenciesPerPage,
  } = props.barrelGetters;
  const { setActivePageCurrencies } = props.barrelSetters;
  const {
    getReviser,
    getWorkplace,
    sendRequest,
    getCurrencies,
  } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Destructured vars
   ============================================================================ */
  const { t } = useTranslation(["CurrencyRegistration"]);

  /** 
   * Yup schema
   ============================================================================ */
  const schema = yup.object({
    curr_abbr: yup
      .string()
      .required(t("Currency abbreviation is required"))
      .min(3, t("Currency abbreviation is too short"))
      .max(5, t("Currency abbreviation is too long")),
    curr_title: yup
      .string()
      .required(t("Currency name is required"))
      .min(3, t("Currency name is too short")) // must be at least 3 characters
      .max(70, t("Currency name is too long")), // must be at most 70 characters
    curr_region: yup
      .string()
      .required(t("Currency region is required"))
      .min(3, t("Currency region is too short")) // must be at least 3 characters
      .max(70, t("Currency region is too long")), // must be at most 70 characters
    curr_rate: yup
      .string()
      .required(t("Exchange rate is required"))
      .matches(
        new RegExp(t("Exchange Rate Regex")),
        t("Wrong exchange rate format")
      ),
  });

  /**
   * Functions
   ============================================================================ */
  const onSubmit = (data) => {
    console.log(data);
    data.reviser = getReviser();
    data.workplace = getWorkplace();

    return sendRequest({ req: props.request, data });
  };

  const indication = () => {
    return "THE TABLE IS EMPTY";
  };

  /**
   * Form
   ============================================================================ */
  const CurrencyRegistrationForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <>
      <Form noValidate onSubmit={handleSubmit}>
        {/* Currency abbreviation */}
        <Form.Group controlId="validationFormikCurrAbbr">
          <Form.Label>{t("Abbreviation")}</Form.Label>
          <InputGroup>
            <Form.Control
              type="string"
              placeholder={""}
              aria-describedby="inputGroupPrepend"
              name="curr_abbr"
              value={values.curr_abbr}
              onChange={handleChange}
              isInvalid={!!errors.curr_abbr && touched.curr_abbr}
            />
            <Form.Control.Feedback type="invalid">
              {errors.curr_abbr}
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>

        {/* Currency name */}
        <Form.Group controlId="validationFormikCurrName">
          <Form.Label>{t("Currency Name")}</Form.Label>
          <InputGroup>
            <Form.Control
              type="string"
              placeholder={""}
              aria-describedby="inputGroupPrepend"
              name="curr_title"
              value={values.curr_title}
              onChange={handleChange}
              isInvalid={!!errors.curr_title && touched.curr_title}
            />
            <Form.Control.Feedback type="invalid">
              {errors.curr_title}
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>

        {/* Currency region */}
        <Form.Group controlId="validationFormikCurrRegion">
          <Form.Label>{t("Currency Region")}</Form.Label>
          <InputGroup>
            <Form.Control
              type="string"
              placeholder={""}
              aria-describedby="inputGroupPrepend"
              name="curr_region"
              value={values.curr_region}
              onChange={handleChange}
              isInvalid={!!errors.curr_region && touched.curr_region}
            />
            <Form.Control.Feedback type="invalid">
              {errors.curr_region}
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>

        {/* Initial exchange rate */}
        <Form.Group controlId="validationFormikCurrRate">
          <Form.Label>{t("Initial Exchange Rate")}</Form.Label>
          <InputGroup>
            <Form.Control
              type="string"
              placeholder={""}
              aria-describedby="inputGroupPrepend"
              name="curr_rate"
              value={values.curr_rate}
              onChange={handleChange}
              isInvalid={!!errors.curr_rate && touched.curr_rate}
            />
            <Form.Control.Feedback type="invalid">
              {errors.curr_rate}
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>

        {/* Submit button */}

        <SubmitButton
          {...args}
          caption={t("Submit")}
          request={props.request}
          namespace={props.namespace}
        />
      </Form>
    </>
  );

  /**
   * Formik return
   ============================================================================ */
  return (
    <Formik
      component={CurrencyRegistrationForm}
      validationSchema={schema}
      onSubmit={onSubmit}
      initialValues={{
        curr_abbr: "",
        curr_title: "",
        curr_region: "",
        curr_rate: 1.0,
      }}
    />
  );
};
