import React, { createRef } from "react";
import { useTranslation } from "react-i18next";
import { Form, InputGroup, Button, Toast } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
// import Card from "../card";
import SubmitButton from "../submitButton";

export default (args) => {
  /**
   * Default Properties
   */
  let defaults = {
    request: "register shareholder",
    namespace: "ShareholderRegistration",
  };

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  const barrels = props;
  const { purposeID } = props;

  /**
   * Barrels
   */
  const { accountNumber, request } = barrels.barrelGetters;
  // const {} = barrels.barrelSetters;
  const {
    // format,
    // parseLocaleNumber,
    getReviser,
    getWorkplace,
    sendRequest,
  } = barrels.barrelFunctions;
  const {} = barrels.barrelEventHandlers;

  // const userDataParsed = JSON.parse(userData);
  // const userDataParsed = userData;
  const { t } = useTranslation([props.namespace]);

  // const thisRef = createRef();

  const schema = yup.object({
    personalID: yup
      .string()
      .required(t("Personal ID is required"))
      .matches(
        new RegExp(t("Personal ID Regex")),
        t("Wrong Personal ID format")
      ),
    shareholderRoles: yup.string().optional(),
    shares: yup
      .number()
      .typeError(t("Shares must be a number"))
      .required(t("Shares is required"))
      .min(0, t("Shares too few"))
      .max(1000, t("Shares too many")),
  });

  /**
   * Send shareholder registration request
   * @param {object} data
   */
  const onSubmit = (data) => {
    data.reviser = getReviser();
    data.workplace = getWorkplace();
    data.uid = accountNumber;
    data.purposeID = purposeID;

    // console.log("REGISTER SHAREHOLDER DATA:", data);

    // onShareholderRegistration({ req: props.request, data: data });
    sendRequest({ req: props.request, data: data });
  };

  const RegisterShareholderForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    errors,
  }) => (
    <Form id="shareholderRegistrationForm" noValidate onSubmit={handleSubmit}>
      <Toast style={{ minWidth: "100%" }}>
        {/*
        <Toast.Header closeButton={false}>
          <strong className="mr-auto">{t("Add Shareholder")}</strong>
        </Toast.Header>
        */}
        <Toast.Body>
          <Form.Group controlId="validationFormikShareholderPersonalID">
            <Form.Label>{t("Shareholder Personal ID")}</Form.Label>
            <InputGroup>
              <InputGroup.Prepend>
                <InputGroup.Text id="inputGroupPrepend">ID</InputGroup.Text>
              </InputGroup.Prepend>
              <Form.Control
                type="text"
                placeholder={t("XXXXXX-XXXXX")}
                aria-describedby="inputGroupPrepend"
                name="personalID"
                value={values.personalID}
                onChange={handleChange}
                isInvalid={!!errors.personalID && touched.personalID}
              />
              <Form.Control.Feedback type="invalid">
                {errors.personalID}
              </Form.Control.Feedback>
            </InputGroup>
          </Form.Group>
          {/* Shareholder Roles */}
          <Form.Group controlId="validationFormikShareholderRoles">
            <Form.Label>{t("Shareholder Roles")}</Form.Label>
            <InputGroup>
              <Form.Control
                type="text"
                placeholder=""
                aria-describedby="inputGroupPrepend"
                name="shareholderRoles"
                value={values.shareholderRoles}
                onChange={handleChange}
                isInvalid={
                  !!errors.shareholderRoles && touched.shareholderRoles
                }
              />
              <Form.Control.Feedback type="invalid">
                {errors.shareholderRoles}
              </Form.Control.Feedback>
            </InputGroup>
          </Form.Group>

          {/* Shares */}
          <Form.Group controlId="validationFormikShareholderRoles">
            <Form.Label>{t("Shares")}</Form.Label>
            <InputGroup>
              <Form.Control
                type="number"
                min={0}
                max={1000}
                step={1}
                placeholder=""
                aria-describedby="inputGroupPrepend"
                name="shares"
                value={values.shares}
                onChange={handleChange}
                isInvalid={!!errors.shares && touched.shares}
              />
              <Form.Control.Feedback type="invalid">
                {errors.shares}
              </Form.Control.Feedback>
            </InputGroup>
          </Form.Group>

          <SubmitButton
            {...args}
            caption={t("Submit")}
            request={props.request}
            namespace={props.namespace}
          />
        </Toast.Body>
      </Toast>
    </Form>
  );

  return (
    <Formik
      component={RegisterShareholderForm}
      validationSchema={schema}
      onSubmit={onSubmit}
      initialValues={{
        personalID: "",
        shares: 0,
        shareholderRoles: "",
      }}
    />
  );
};
