/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { Button, Spinner } from "react-bootstrap";

/**
  * User imports
  ============================================================================ */

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = {
    caption: "Submit",
    request: null,
    namespace: "Button",
    visible: true,
    marginTop: 0,
  };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { request } = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]); // adjust namespace

  /**
    * Functions
    ============================================================================ */
  const getJSX = () => {
    return !props.visible ? (
      <></>
    ) : (
      <Button
        type="submit"
        block
        disabled={request === props.request}
        style={{ marginTop: props.marginTop + "px" }}
      >
        <Spinner
          as="span"
          animation="border"
          size="sm"
          role="status"
          aria-hidden="true"
          hidden={request !== props.request}
        />
        &nbsp;
        {props.caption}
        &nbsp;
      </Button>
    );
  };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
};
