export default ({ args, barrels }) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {};

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  // console.log("RESPONSE HANDLER: ", props, barrels);

  /**
   * Barrels
   ============================================================================ */
  const {
    debug,
    currencies,
    transactions,
    shares,
    transactionsPerPage,
    sharesPerPage,
    userData,
    accountNumber,
    uid,
    userBusinesses,
    businessPurposes,
    purposeShareholders,
    activeBusinesPurpose,
    lastInsertId,
  } = barrels.barrelGetters;
  const {
    setRequest,
    setSession,
    setCurrencies,
    setTransactions,
    setShares,
    setCountCurrencies,
    setCountTransactions,
    setCountShares,
    setConnected,
    // setLanguage,
    setUserData,
    setSignedIn,
    setCurrencyAbbr,
    setCurrencyRate,
    // setCurrencyStatus,
    // setTransferSuccess,
    setRedirectTo,
    setLastInsertId,
    setAccountNumber,
    setUID,
    setUserBusinesses,
    setBusinessPurposes,
    setPurposeShareholders,
    // setBusinessLoaded,
    setAccountPhotoPath,
    setPasswordResetTokenValidated,
    setPasswordResetTokenEmail,
    // setRedirect,
  } = barrels.barrelSetters;
  const {
    sendRequest,
    // processResponse,
    getBusinesses,
    getAccountData,
    getTransactionById,
    getBusinessPurposes,
    getPurposeShareholders,
    showMessage,
    changeLanguage,
    updateTimestamp,
    // hideMessage,
  } = barrels.barrelFunctions;
  const {
    onTransferSuccess,
    onRedirect,
    onLogOut,
  } = barrels.barrelEventHandlers;

  /**
   * Hooks
   ============================================================================ */
  const { req, status } = props;
  // const { userData, accountNumber } = barrels.barrelGetters;

  // const userDataParsed = JSON.parse(userData);
  // const userDataParsed = userData;
  // console.log("USER DATA PARSED:", userDataParsed);

  /**
   * Log response
   */
  if (debug) console.log(`response: ${req}`, props, barrels);

  let responseFound = false;

  /** 
   * Request event dispatcher
   ============================================================================ */
  const onResponse = (data, callback) => {
    if (responseFound) return;
    const [res, status] = data;
    if ((res === props.req && status === props.status) || res === "unknown") {
      if (callback()) {
        setRequest(null);
      }
      responseFound = true;
      // return true;
    }
    // return false;
  };

  /** 
   * Response handlers
  ============================================================================ */

  /**
   * Start session (success)
   */
  onResponse(["start session", "success"], () => {
    setConnected(true);
    return true;
  });

  /**
   * End session (success)
   */
  onResponse(["end session", "success"], () => {
    console.log("ON END SESSION");

    setConnected(false);
    return true;
  });

  /**
   * Register user (pending)
   */
  onResponse(["register user", "pending"], () => {
    // Might show some user feedback
    return false;
  });

  onResponse(["register user", "continue"], () => {
    if (props.name === "INSERT INTO users") {
      if (props.results.insertId > 0) {
        // setLastInsertId(props.results.insertId);
        return false;
      } else {
        // No row was inserted - an error was returned
        return true;
      }
    }
    return false;
  });

  /**
   * Register user (success)
   */
  onResponse(["register user", "success"], () => {
    // console.log("REGISTER USER PROPS:", props);
    const noEmailValidation = false;

    if (noEmailValidation) {
      setSignedIn(true);
      setSession(props.email);
      onRedirect("/account-registration");
      sendRequest({ req: "get user", data: { email: props.email } }); // to not require a log in
      return false;
    } else {
      onRedirect("/validate-email");
      return true;
    }
  });

  onResponse(["validate email token", "continue"], () => {
    if (props.name === "UPDATE validation tokens") {
      if (props.results.affectedRows > 0) {
        // setLastInsertId(props.results.insertId);
        // onRedirect("/sign-in");
        return false;
      } else {
        // No row was inserted - an error was returned
        return true;
      }
    }

    if (props.name === "UPDATE users") {
      if (props.results.affectedRows > 0) {
        // setLastInsertId(props.results.insertId);
        onRedirect("/sign-in");
        return true;
      } else {
        // No row was inserted - an error was returned
        return true;
      }
    }

    return false;
  });

  // onResponse(["validate password reset token", "continue"], () => {
  //   if (props.name === "UPDATE validation tokens") {
  //     if (props.results.affectedRows > 0) {
  //       // onRedirect("/password");
  //       setPasswordResetTokenValidated(true);
  //       return true;
  //     } else {
  //       // No row was inserted - an error was returned
  //       return true;
  //     }
  //   }

  //   return false;
  // });

  onResponse(["validate password reset token", "success"], () => {
    setPasswordResetTokenValidated(true);

    return true;
  });

  /**
   * Sign in user (continue)
   */
  onResponse(["sign in user", "continue"], () => {
    // user with validated email
    if (props.data.status === 1) {
      setUserData(JSON.stringify(props.data));
      setAccountNumber(props.data.uid);
      setSession(props.data.email);
      setCurrencyAbbr(props.data.currency_abbr);
      // setCurrencyRate(props.data.currency_rate);
      setCurrencyRate(
        isNaN(props.data.currency_rate) ? 1 : props.data.currency_rate
      );
      // setCurrencyStatus(props.data.currency_status);
      changeLanguage(props.data.language);
      setSignedIn(true);
      onRedirect("/summary");
      return false;
    }

    // user without validated email
    if (props.data.status === 0) {
      onRedirect("/validate-email");
      return true;
    }

    // suspended or deleted user
    if (props.data.status === 2) {
      // onLogOut();
      setRedirectTo("/sign-out");
      showMessage("ACCOUNT_SUSPENDED", "error");
      return true;
    }

    // suspended or deleted user
    if (props.data.status === 3) {
      // onLogOut();
      setRedirectTo("/sign-out");
      showMessage("ACCOUNT_DELETED", "error");
      return true;
    }
  });

  /**
   * Sign in user (finished)
   */
  onResponse(["sign in user", "finished"], () => {
    if (props.rowNum === 1) {
      // setSignedIn(true);
      // onRedirect("/summary");
      return true;
    } else {
      // TODO: Clean Log Out() user here
      // setSignedIn(false);

      // onLogOut();
      showMessage("INVALID_SIGN_IN", "error");
      setRedirectTo("/sign-out");

      return true;
    }
  });

  /**
   * Update user (success)
   */
  onResponse(["update user", "success"], () => {
    // setUserData(JSON.stringify(props.data));
    sendRequest({ req: "get user", data: { email: userData.email } });
    return false;
  });

  /**
   * Get user (continue)
   */
  onResponse(["get user", "continue"], () => {
    setUserData(JSON.stringify(props.data));
    return false;
  });

  /**
   * Get user (finished)
   */
  onResponse(["get user", "finished"], () => {
    if (props.rowNum === 1) {
      return true;
    } else {
      // Something wrong with (accessing) the user data
      // Probable causes:
      // - user was deleted from the DB
      // - user status was set to >= 2
      // - multiple users with the same email address, most improbable (db constraint)
      // - user changed the email address or password
      // onLogOut();
      setRedirectTo("/sign-out");
      // showMessage("INVALID_DATA", "error");
      return true;
    }
  });

  /**
   * Get user businesses (continue)
   */
  onResponse(["get user businesses", "continue"], () => {
    if (userBusinesses === null) {
      setUserBusinesses(JSON.stringify([props.data]));
    } else {
      setUserBusinesses(JSON.stringify([...userBusinesses, props.data]));
    }

    // getBusinessPurposes(props.data.business_id);

    return false;
  });

  /**
   * Get user businesses (finished)
   */
  onResponse(["get user businesses", "finished"], () => {
    setRedirectTo("/my-business");
    return true;
  });

  /**
   * Get business purposes (continue)
   */
  onResponse(["get business purposes", "continue"], () => {
    if (businessPurposes === null) {
      setBusinessPurposes(JSON.stringify([props.data]));
    } else {
      setBusinessPurposes(JSON.stringify([...businessPurposes, props.data]));
    }

    // getPurposeShareholders(props.data.purpose_id);

    return false;
  });

  /**
   * Get business purposes (finished)
   */
  onResponse(["get business purposes", "finished"], () => {
    return true;
  });

  /**
   * Get purpose shareholders (continue)
   */
  onResponse(["get purpose shareholders", "continue"], () => {
    if (purposeShareholders === null) {
      setPurposeShareholders(JSON.stringify([props.data]));
    } else {
      setPurposeShareholders(
        JSON.stringify([...purposeShareholders, props.data])
      );
    }

    return false;
  });

  /**
   * Get purpose shareholders (finished)
   */
  onResponse(["get purpose shareholders", "finished"], () => {
    return true;
  });

  /**
   * Get user business by id (continue)
   */
  onResponse(["get business by id", "continue"], () => {
    if (userBusinesses === null) {
      setUserBusinesses(JSON.stringify([props.data]));
    } else {
      setUserBusinesses(JSON.stringify([...userBusinesses, props.data]));
    }

    return false;
  });

  /**
   * Get business by id (finished)
   */
  onResponse(["get business by id", "finished"], () => {
    return true;
  });

  /**
   * Get purpose by id (continue)
   */
  onResponse(["get purpose by id", "continue"], () => {
    if (businessPurposes === null) {
      setBusinessPurposes(JSON.stringify([props.data]));
    } else {
      setBusinessPurposes(JSON.stringify([...businessPurposes, props.data]));
    }

    return false;
  });

  /**
   * Get purpose by id (finished)
   */
  onResponse(["get purpose by id", "finished"], () => {
    return true;
  });

  /**
   * Get shareholder by id (continue)
   */
  onResponse(["get shareholder by id", "continue"], () => {
    if (purposeShareholders === null) {
      setPurposeShareholders(JSON.stringify([props.data]));
    } else {
      setPurposeShareholders(
        JSON.stringify([...purposeShareholders, props.data])
      );
    }

    return false;
  });

  /**
   * Get shareholder by id (finished)
   */
  onResponse(["get shareholder by id", "finished"], () => {
    return true;
  });

  /**
   * Upload file (ready)
   */
  onResponse(["upload file", "continue"], () => {
    // setAccountPhotoPath(props + "?r=" + Math.random());
    setAccountPhotoPath("ok");
    return true;
  });

  /**
   * Register account (success)
   */
  onResponse(["register account", "continue"], () => {
    if (props.results.affectedRows < 1) {
      // error registering account
      setUID(null);
      setAccountNumber(null);
      showMessage("INVALID_DATA", "error");
      return true;
    }
    return false;
  });

  /**
   * Register account (success)
   */
  onResponse(["register account", "success"], () => {
    // setAccountNumber(props.uid);
    // setUID(props.uid);
    // sendRequest({
    //   req: "get account data",
    //   data: { uid: accountNumber },
    // });
    getAccountData();
    // onRedirect("/balance");
    return false;
  });

  /**
   * Get account data (continue)
   */
  onResponse(["get account data", "continue"], () => {
    // setUserData(JSON.stringify(props.data));
    // return false;

    if (props.rowNum === 1) {
      // all is good
      setUserData(JSON.stringify(props.data));
      return false;
    } else {
      // Account not reachable
      showMessage("INVALID_DATA", "error");
      return true;
    }
  });

  /**
   * Get account data (finished)
   */
  onResponse(["get account data", "finished"], () => {
    // if (props.rowNum === 1) {
    //   // all is good
    //   return true;
    // } else {
    //   // Account not reachable
    //   showMessage("INVALID_DATA", "error");
    //   return true;
    // }
    return true;
  });

  /**
   * Transfer U2S (pending)
   ============================================================================================*/
  onResponse(["transfer U2S", "pending"], () => {
    // might show some pending state feedback
    return false;
  });

  /**
   * Transfer U2S (continue)
   */
  onResponse(["transfer U2S", "continue"], () => {
    if (props.name === "INSERT INTO transactions") {
      if (props.results.insertId > 0) {
        setLastInsertId(props.results.insertId);
        return false;
      } else {
        // No row was inserted - an error was returned
        return true;
      }
    }
    return false;
  });

  /**
   * Transfer U2S (success)
   */
  onResponse(["transfer U2S", "success"], () => {
    getTransactionById(lastInsertId);
    return false;
  });

  /**
   * Transfer U2U (pending)
   ============================================================================================*/
  onResponse(["transfer U2U", "pending"], () => {
    // might show some pending state feedback
    return false;
  });

  /**
   * Transfer U2U (continue)
   */
  onResponse(["transfer U2U", "continue"], () => {
    if (props.name === "INSERT INTO transactions") {
      if (props.results.insertId > 0) {
        setLastInsertId(props.results.insertId);
        return false;
      } else {
        // No row was inserted - an error was returned
        return true;
      }
    }
    return false;
  });

  /**
   * Transfer U2U (success)
   */
  onResponse(["transfer U2U", "success"], () => {
    getTransactionById(lastInsertId);
    return false;
  });

  /**
   * Transfer U2B (pending)
   ============================================================================================*/
  onResponse(["transfer U2B", "pending"], () => {
    // might show some pending state feedback
    return false;
  });

  /**
   * Transfer U2B (continue)
   */
  onResponse(["transfer U2B", "continue"], () => {
    if (props.name === "INSERT INTO transactions") {
      if (props.results.insertId > 0) {
        setLastInsertId(props.results.insertId);
        return false;
      } else {
        // No row was inserted - an error was returned
        return true;
      }
    }
    return false;
  });

  /**
   * Transfer U2B (success)
   */
  onResponse(["transfer U2B", "success"], () => {
    getTransactionById(lastInsertId);
    return false;
  });

  /**
   * Register business
   ============================================================================================*/
  onResponse(["register business", "continue"], () => {
    // console.log("RESPONSE register business:", props);

    if (props.results.insertId > 0) {
      // showMessage("REGISTER_BUSINESS_SUCCESS", "info");

      // sendRequest({
      //   req: "get business by id",
      //   data: { id: props.results.insertId },
      // });
      setLastInsertId(props.results.insertId);
      return false;
    } else {
      // No row was inserted - an error was returned
      return true;
    }
  });

  onResponse(["register business", "success"], () => {
    // console.log("RESPONSE register business:", props);

    sendRequest({
      req: "get business by id",
      data: { id: lastInsertId },
    });
    // getBusinesses();
    return false;
    // return true;
  });

  onResponse(["register currency", "continue"], () => {
    // console.log("RESPONSE register business:", props);

    if (props.results.insertId > 0) {
      setLastInsertId(props.results.insertId);

      return false;
    } else {
      // No row was inserted - an error was returned
      return true;
    }
  });

  onResponse(["register currency", "success"], () => {
    sendRequest({
      req: "get currency by id",
      data: { id: lastInsertId },
    });
    return false;
  });

  /**
   * Get currencies (continue)
   */
  onResponse(["get currencies", "continue"], () => {
    // console.log("PROPS NAME:", props.name);

    if (props.name === "SELECT currencies") {
      if (currencies === null) {
        setCurrencies(JSON.stringify([props.results]));
      } else {
        setCurrencies(JSON.stringify([...currencies, props.results]));
      }
    } else if (props.name === "COUNT currencies") {
      setCountCurrencies(props.results.count_currencies);
    }

    return false;
  });

  /**
   * Get currencies (finished)
   */
  onResponse(["get currencies", "finished"], () => {
    setRedirectTo("/currency");
    return true;
  });

  /**
   * Get currency by id (continue)
   */
  onResponse(["get currency by id", "continue"], () => {
    if (currencies === null) {
      setCurrencies(JSON.stringify([props.data]));
    } else {
      setCurrencies(JSON.stringify([...currencies, props.data]));
    }

    return false;
  });

  /**
   * Get currency by id (finished)
   */
  onResponse(["get currency by id", "finished"], () => {
    return true;
  });

  /**
   * Get transactions (continue)
   */
  onResponse(["get transactions", "continue"], () => {
    // console.log("PROPS NAME:", props.name);

    if (props.name === "SELECT transactions") {
      if (transactions === null) {
        setTransactions(JSON.stringify([props.results]));
      } else {
        setTransactions(JSON.stringify([...transactions, props.results]));
      }
    } else if (props.name === "COUNT transactions") {
      setCountTransactions(props.results.count_transaction_positions);
    }

    return false;
  });

  /**
   * Get transactions (finished)
   */
  onResponse(["get transactions", "finished"], () => {
    setRedirectTo("/balance");
    return true;
  });

  /**
   * Get shares (continue)
   */
  onResponse(["get shares", "continue"], () => {
    // console.log("PROPS NAME:", props.name);

    if (props.name === "SELECT shares") {
      if (shares === null) {
        setShares(JSON.stringify([props.results]));
      } else {
        setShares(JSON.stringify([...shares, props.results]));
      }
    } else if (props.name === "COUNT shares") {
      setCountShares(props.results.count_shares);
    }

    return false;
  });

  /**
   * Get shares (finished)
   */
  onResponse(["get shares", "finished"], () => {
    // setRedirectTo("/balance");
    return true;
  });

  /**
   * Get transaction by id (continue)
   */
  onResponse(["get transaction by id", "continue"], () => {
    if (transactions === null) {
      setTransactions(JSON.stringify([props.data]));
    } else {
      setTransactions(JSON.stringify([...transactions, props.data]));
    }

    return false;
  });

  /**
   * Get currency by id (finished)
   */
  onResponse(["get transaction by id", "finished"], () => {
    getAccountData();
    return false;
  });

  onResponse(["register purpose", "continue"], () => {
    // sendRequest({
    //   req: "get purpose by id",
    //   data: { id: props.results.insertId },
    // });
    if (props.results.insertId > 0) {
      setLastInsertId(props.results.insertId);
      return false;
    } else {
      return true;
    }
  });

  onResponse(["register purpose", "success"], () => {
    sendRequest({
      req: "get purpose by id",
      data: { id: lastInsertId },
    });
    return false;
  });

  onResponse(["register shareholder", "continue"], () => {
    if (props.results.insertId > 0) {
      // showMessage("REGISTER_SHAREHOLDER_SUCCESS", "info");

      // return sendRequest({
      //   req: "get shareholder by id",
      //   data: { id: props.results.insertId },
      // });
      setLastInsertId(props.results.insertId);
    } else {
      // No row was inserted - an error was returned
      return true;
    }
  });

  onResponse(["register shareholder", "success"], () => {
    // sendRequest({
    //   req: "get shareholder by id",
    //   data: { id: lastInsertId },
    // });

    getPurposeShareholders(activeBusinesPurpose);
    return false;
  });

  onResponse(["change share", "success"], () => {
    // sendRequest({
    //   req: "get shareholder by id",
    //   data: { id: lastInsertId },
    // });

    // getPurposeShareholders(activeBusinesPurpose);
    return true;
  });

  onResponse(["remove shareholder", "success"], () => {
    // sendRequest({
    //   req: "get shareholder by id",
    //   data: { id: lastInsertId },
    // });

    getPurposeShareholders(activeBusinesPurpose);
    return false;
  });

  onResponse(["reset password", "success"], () => {
    setPasswordResetTokenValidated(false);
    return true;
  });

  onResponse(["change password", "success"], () => {
    // TOTO: If NO_ROWS_CHANGE - probably the currently saved password was submitted
    setPasswordResetTokenValidated(null);
    setPasswordResetTokenEmail(null);
    onRedirect("/sign-in");
    showMessage("PASSWORD_CHANGE_SUCCESS", "info");
    return true;
  });

  // onResponse(["unknown"], () => {
  //   if (
  //     status === "pending" ||
  //     status === "continue" ||
  //     status === "finished" ||
  //     status === "success"
  //   ) {
  //     return false;
  //   } else {
  //     showMessage(`${req}: ${status}`, "info");
  //     return true;
  //   }
  // });

  onResponse(["unknown"], () => {
    return false;
  });

  return responseFound;
};
