import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";

export default (args) => {
  /**
   * Default Properties
   */
  let defaults = {};

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  /**
   * Custom hooks
   */
  const { t } = useTranslation("Logout");

  // console.log("ALERT DATA", props);

  /**
   * Barrels
   */
  const { error, language, loggingOut } = props.barrelGetters;
  const { setLanguage } = props.barrelSetters;
  const { showMessage } = props.barrelFunctions;
  const { onLogOut } = props.barrelEventHandlers;

  /**
   * Default Properties
   */
  // let defaults = {};
  // const props = Object.assign(defaults, authorizedData);

  // const [logOut] = useState(true);

  // console.log("sendUserLogOutRequest", props);

  useEffect(() => {
    // if (logOut !== true) return;
    // console.log("LOG OUT =============");

    const tempError = error;
    const tempLanguage = language;

    onLogOut();

    setLanguage(tempLanguage);

    if (tempError !== null) {
      showMessage(tempError, "error");
    }
    // eslint-disable-next-line
  }, []); // logOut

  return (
    <>
      <h4>{t("See you soon")}</h4>
      <hr />

      <br />
      <p className="text-center">
        <a href="/">{t("Sign in")}</a>
      </p>
    </>
  );
};
