/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { Form, Button, Badge, Alert, Accordion, Card } from "react-bootstrap";

/**
  * User imports
  ============================================================================ */
// import Card from "../card";
import Currencies from "../currencies";
import CurrencyRegistration from "../currencyRegistration";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "CurrencyContainer" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const {
    accordionViewCurrency,
    activePageCurrencies,
    accountNumber,
  } = props.barrelGetters;
  const { setAccordionViewCurrency, setRedirectTo } = props.barrelSetters;
  const { getCurrencies } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
    * Functions
    ============================================================================ */
  const ToggleCurrencies = () => {
    // console.log("decorate");
    setAccordionViewCurrency("currencies");
    getCurrencies(activePageCurrencies);
  };

  const ToggleCurrencyRegistration = () => {
    // console.log("decorate");
    setAccordionViewCurrency("currencyRegistration");
    // getTransactions(1);
  };

  if (accountNumber === null) setRedirectTo("/sign-out");

  /**
    * Return
    ============================================================================ */
  return (
    <>
      <Accordion defaultActiveKey={accordionViewCurrency}>
        <Card>
          <Accordion.Toggle
            as={Card.Header}
            eventKey="currencies"
            style={{ cursor: "pointer" }}
            onClick={ToggleCurrencies}
          >
            {t("Currencies")}
          </Accordion.Toggle>

          <Accordion.Collapse eventKey="currencies">
            <Card.Body>
              <Currencies {...args} />
            </Card.Body>
          </Accordion.Collapse>
        </Card>

        <Card>
          <Accordion.Toggle
            as={Card.Header}
            eventKey="currencyRegistration"
            style={{ cursor: "pointer" }}
            onClick={ToggleCurrencyRegistration}
          >
            {t("Currency Registration")}
          </Accordion.Toggle>

          <Accordion.Collapse eventKey="currencyRegistration">
            <Card.Body>
              <CurrencyRegistration {...args} />
            </Card.Body>
          </Accordion.Collapse>
        </Card>
      </Accordion>
    </>
  );
};
