import "bootstrap/dist/css/bootstrap.min.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import Pagination from "react-bootstrap/Pagination";

/**
  * User imports
  ============================================================================ */

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = {
    namespace: "Pagination",
    pages: 0,
    active: null,
    onClick: () => {},
    align: "center",
  };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  if (props.pages < 2) return <></>;

  /**
    * Barrels
    ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  const getPages = () => {
    let items = [];
    for (let number = 1; number <= props.pages; number++) {
      if (
        number === 1 ||
        number === props.pages ||
        (number >= props.active - 2 && number <= props.active + 2)
      ) {
        items.push(
          <Pagination.Item
            key={number}
            active={number === props.active}
            onClick={() => props.onClick(number)}
          >
            {number}
          </Pagination.Item>
        );
      } else if (number === 2 || number === props.pages - 1) {
        items.push(<Pagination.Ellipsis key={number} disabled />);
      }
    }
    return items;
  };

  //   <Pagination>
  //   <Pagination.First />
  //   <Pagination.Prev />

  //   {getPages()}

  //   <Pagination.Item>{1}</Pagination.Item>
  //   <Pagination.Ellipsis />

  //   <Pagination.Item>{10}</Pagination.Item>
  //   <Pagination.Item>{11}</Pagination.Item>
  //   <Pagination.Item active>{12}</Pagination.Item>
  //   <Pagination.Item>{13}</Pagination.Item>
  //   <Pagination.Item disabled>{14}</Pagination.Item>

  //   <Pagination.Ellipsis />
  //   <Pagination.Item>{20}</Pagination.Item>
  //   <Pagination.Next />
  //   <Pagination.Last />
  // </Pagination>

  /**
    * Functions
    ============================================================================ */
  const getJSX = () => {
    const { align } = props;

    return (
      <div
        style={{
          textAlign: `${align}`,
          // backgroundColor: "red",
        }}
      >
        <span style={{ display: "inline-block" }}>
          <Pagination style={{ padding: 0, margin: 0 }}>
            {props.pages > 5 && (
              <>
                <Pagination.First onClick={() => props.onClick("First")} />
                <Pagination.Prev onClick={() => props.onClick("Prev")} />
              </>
            )}

            {getPages()}

            {props.pages > 5 && (
              <>
                <Pagination.Next onClick={() => props.onClick("Next")} />
                <Pagination.Last onClick={() => props.onClick("Last")} />
              </>
            )}
          </Pagination>
        </span>
      </div>
    );
  };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
};
