import "./UserEditor.css";
import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { ListGroup, Form, InputGroup, Button } from "react-bootstrap";
import {
  Visibility,
  VisibilityOff,
  Close,
  Edit,
  Done,
} from "@material-ui/icons";
import { Formik } from "formik";
import * as yup from "yup";
import Card from "../card";
import SubmitButton from "../submitButton";

export default (args) => {
  /**
   * Default Properties
   */
  let defaults = {
    namespace: "UserEditor",
    request: "update user",
    mode: "label",
  };

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  // console.log("USER EDITOR PROPS:", props);

  /**
   * Barrels
   */
  const {
    userData,
    accountNumber,
    currencies,
    currencyAbbr,
    language,
  } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { sendRequest, getReviser, getWorkplace } = props.barrelFunctions;
  const { onRedirect } = props.barrelEventHandlers;

  /**
   * Hooks
   */
  const [editFirstname, setEditFirstname] = useState(false);
  const [editLastname, setEditLastname] = useState(false);
  const [editEmail, setEditEmail] = useState(false);
  const [editPassword, setEditPassword] = useState(false);
  const [passwordVisible, setPasswordVisible] = useState(false);
  const [submitButtonVisible, setSubmitButtonVisible] = useState(false);

  useEffect(() => {
    if (editFirstname || editLastname || editEmail || editPassword) {
      setSubmitButtonVisible(true);
    } else {
      setSubmitButtonVisible(false);
    }

    // eslint-disable-next-line
  }, [editFirstname, editLastname, editEmail, editPassword]);

  const { t } = useTranslation([props.namespace]);

  /**
   * Functions
   */

  const togglePassword = () => {
    setPasswordVisible(!passwordVisible);
  };

  const sendUserRegistrationRequest = (data) => {
    if (data === null) return false;
    data.reviser = getReviser();
    data.workplace = getWorkplace();
    // data.language = language;
    // data.email = userData.email;
    return sendRequest({ req: props.request, data });
  };

  /**
   * Event listener: On Form Submit
   * @param {object} data Form data
   */
  const onSubmit = (data) => {
    data.personalID = accountNumber;
    if (!editFirstname) data.firstname = null;
    if (!editLastname) data.lastname = null;
    if (!editEmail) data.email = userData.email;
    if (!editPassword) data.password = null;

    // console.log("onSubmit:", data);

    if (sendUserRegistrationRequest(data)) {
      // User registration request was submitted
    }
  };

  const yupObject = {
    firstName: editFirstname
      ? yup.string().required(t("First name is required"))
      : yup.string().optional(),
    lastName: editLastname
      ? yup.string().required(t("Last name is required"))
      : yup.string().optional(),
    email: editEmail
      ? yup
          .string()
          .email(t("Invalid email address"))
          .required(t("Email is required"))
      : yup.string().optional(),
    password: editPassword
      ? yup
          .string()
          .required(t("No password provided"))
          .min(8, t("Password is too short"))
          // .matches(/[a-zA-Z]/, "Password can only contain Latin letters.")
          .matches(
            /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#_?&])[A-Za-z\d@$!%*#_?&]{8,}$/,
            t("Password is too weak")
          )
      : yup.string().optional(),
  };

  /**
   * Yup schema
   */
  const schema = yup.object(yupObject);

  /**
   * Data consistency tests
   */
  if (userData === null) return <></>;

  /**
   * Destructuring
   */
  const { mode } = props;
  const {
    firstname,
    lastname,
    email,
    // language,
    currency_name,
    currency_abbr,
  } = userData;
  const currency = `${currency_name} (${currency_abbr})`;

  const UserEditorForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <Form noValidate onSubmit={handleSubmit}>
      <Card title={t("Profile")}>
        {/* First name */}
        <ListGroup variant="flush">
          <ListGroup.Item as="div" action>
            <Form.Label>{t("First name")}</Form.Label>
            {!editFirstname ? (
              <>
                <span style={{ float: "right" }}>
                  <Edit
                    onClick={() => setEditFirstname(true)}
                    fontSize="small"
                    style={{ color: "var(--dark)", cursor: "pointer" }}
                  />
                </span>
                <br />
                <small>{firstname}</small>
              </>
            ) : (
              <span style={{ float: "right" }}>
                <Close
                  onClick={() => setEditFirstname(false)}
                  fontSize="small"
                  style={{ color: "var(--dark)", cursor: "pointer" }}
                />
              </span>
            )}

            {editFirstname && (
              <Form.Group controlId="validationFormikFirstName">
                <InputGroup>
                  <Form.Control
                    type="text"
                    placeholder={t("Enter first name")}
                    name="firstName"
                    value={values.firstName}
                    onChange={handleChange}
                    isInvalid={!!errors.firstName && touched.firstName}
                  />
                  <Form.Control.Feedback type="invalid">
                    {errors.firstName}
                  </Form.Control.Feedback>
                </InputGroup>
              </Form.Group>
            )}
          </ListGroup.Item>

          {/* Last name */}
          <ListGroup.Item as="div" action>
            <Form.Label>{t("Last name")}</Form.Label>
            {!editLastname ? (
              <>
                <span style={{ float: "right" }}>
                  <Edit
                    onClick={() => setEditLastname(true)}
                    fontSize="small"
                    style={{ color: "var(--dark)", cursor: "pointer" }}
                  />
                </span>
                <br />
                <small>{lastname}</small>
              </>
            ) : (
              <span style={{ float: "right" }}>
                <Close
                  onClick={() => setEditLastname(false)}
                  fontSize="small"
                  style={{ color: "var(--dark)", cursor: "pointer" }}
                />
              </span>
            )}

            {editLastname && (
              <Form.Group controlId="validationFormikLastName">
                <InputGroup>
                  <Form.Control
                    type="text"
                    placeholder={t("Enter last name")}
                    name="lastName"
                    value={values.lastName}
                    onChange={handleChange}
                    isInvalid={!!errors.lastName && touched.lastName}
                  />
                  <Form.Control.Feedback type="invalid">
                    {errors.lastName}
                  </Form.Control.Feedback>
                </InputGroup>
              </Form.Group>
            )}
          </ListGroup.Item>

          {/* Email */}
          {accountNumber !== null && (
            <ListGroup.Item as="div" action>
              <Form.Label>{t("Email address")}</Form.Label>
              {!editEmail ? (
                <>
                  <span style={{ float: "right" }}>
                    <Edit
                      onClick={() => setEditEmail(true)}
                      fontSize="small"
                      style={{ color: "var(--dark)", cursor: "pointer" }}
                    />
                  </span>
                  <br />
                  <small>{email}</small>
                </>
              ) : (
                <span style={{ float: "right" }}>
                  <Close
                    onClick={() => setEditEmail(false)}
                    fontSize="small"
                    style={{ color: "var(--dark)", cursor: "pointer" }}
                  />
                </span>
              )}

              {editEmail && (
                <Form.Group controlId="validationFormikEmail">
                  <InputGroup>
                    <Form.Control
                      placeholder={t("Enter email")}
                      name="email"
                      value={values.email}
                      onChange={handleChange}
                      // onBlur={handleChange}
                      isInvalid={!!errors.email && touched.email}
                    />
                    <Form.Control.Feedback type="invalid">
                      {errors.email}
                    </Form.Control.Feedback>
                  </InputGroup>
                </Form.Group>
              )}
            </ListGroup.Item>
          )}

          {/* Password */}
          <ListGroup.Item as="div" action>
            <Form.Label>{t("Password")}</Form.Label>
            {!editPassword ? (
              <>
                <span style={{ float: "right" }}>
                  <Edit
                    onClick={() => setEditPassword(true)}
                    fontSize="small"
                    style={{ color: "var(--dark)", cursor: "pointer" }}
                  />
                </span>
              </>
            ) : (
              <span style={{ float: "right" }}>
                <Close
                  onClick={() => setEditPassword(false)}
                  fontSize="small"
                  style={{ color: "var(--dark)", cursor: "pointer" }}
                />
              </span>
            )}

            {editPassword && (
              <>
                {/* New password */}
                <Form.Group controlId="validationFormikPassword">
                  <InputGroup>
                    <Form.Control
                      type={passwordVisible ? "text" : "password"}
                      placeholder={t("Enter new password")}
                      name="password"
                      value={values.password}
                      onChange={handleChange}
                      // onBlur={handleChange}
                      isInvalid={!!errors.password && touched.password}
                    />
                    <InputGroup.Append>
                      <InputGroup.Text
                        id="inputGroupAppend"
                        onClick={togglePassword}
                        style={{
                          cursor: "pointer",
                        }}
                      >
                        {passwordVisible ? (
                          <VisibilityOff
                            fontSize="small"
                            style={{ color: "var(--dark)", cursor: "pointer" }}
                          />
                        ) : (
                          <Visibility
                            fontSize="small"
                            style={{ color: "var(--dark)", cursor: "pointer" }}
                          />
                        )}
                      </InputGroup.Text>
                    </InputGroup.Append>

                    <Form.Control.Feedback type="invalid">
                      {errors.password}
                    </Form.Control.Feedback>
                  </InputGroup>
                </Form.Group>
              </>
            )}
          </ListGroup.Item>
        </ListGroup>

        <Form.Group controlId="validationSubmitButton">
          <SubmitButton
            {...args}
            caption={t("Submit")}
            visible={submitButtonVisible}
            marginTop={20}
            request={props.request}
            namespace={props.namespace}
          />
        </Form.Group>
      </Card>
    </Form>
  );

  const jsx =
    mode == "label" ? (
      `${firstname} ${lastname}`
    ) : (
      <Formik
        component={UserEditorForm}
        validationSchema={schema}
        onSubmit={onSubmit}
        initialValues={{
          firstName: userData.firstname,
          lastName: userData.lastname,
          email: userData.email,
          password: "",
        }}
      />
    );

  return <>{jsx}</>;
};
