const dev = {
  uri: {
    ENDPOINT: "http://localhost:9000",
    // ENDPOINT: "http://192.168.2.100:9000"
  },
};

const dev2 = {
  uri: {
    ENDPOINT: "http://192.168.2.100:5000",
  },
};

const staging = {
  uri: {
    ENDPOINT: "https://dev.zime.lv",
  },
};

const prod = {
  uri: {
    ENDPOINT: "https://zime.lv",
  },
};

const hostname = window && window.location && window.location.hostname;

let config;
switch (hostname) {
  case "localhost":
    config = dev;
    break;
  case "192.168.2.100":
    config = dev2;
    break;
  case "dev.zime.lv":
    config = staging;
    break;
  case "zime.lv":
    config = prod;
    break;
  default:
    config = dev;
    break;
}

export default {
  ...config,
};
