/**
 * System imports
 ============================================================================ */
import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";
import { BrowserRouter as Router, Route } from "react-router-dom";
import { Spinner } from "react-bootstrap";

/**
  * User imports
  ============================================================================ */
import Card from "../card";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "ValidateEmail" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { validateTokenEmail } = props.barrelGetters;
  const { setValidateTokenEmail } = props.barrelSetters;
  const { getUrlParam, sendEmailValidationRequest } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
    * Hooks
    ============================================================================ */

  // setToken(getUrlParam("token"));
  // setEmail(getUrlParam("email"));

  const token = getUrlParam("token");
  const email = getUrlParam("email");

  /**
    * Effects
    ============================================================================ */
  useEffect(() => {
    if (token === null || email === null) return;

    setValidateTokenEmail(
      JSON.stringify({
        token,
        email,
      })
    );
    // eslint-disable-next-line
  }, []);

  /**
    * Functions
    ============================================================================ */
  const getJSX = () => {
    // const queryString = window.location.search;
    // const urlParams = new URLSearchParams(queryString);

    return token === null ? (
      <>
        <h4>{t("A verification link has been sent to your email account.")}</h4>
        <hr />
        <p>
          {t(
            "Please click on the link that has just been sent to your email account"
          )}
        </p>
      </>
    ) : (
      <div className="text-center">
        <Spinner
          as="span"
          animation="border"
          size="sm"
          role="status"
          aria-hidden="true"
          hidden={false}
        />
      </div>
    );
  };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
};
